    }
    .header .logo{
        font-size: 2.5rem;
        margin-right: 12rem;
    }
    .header .navbar{
        padding-top: 3.5rem;
        margin-left: 5rem;
    }
    .info{
        margin-left: 7rem;
        margin-top: 7rem;
        width: 80%;
    }
    .info h1{
        font-weight: bolder;
        font-size: 4rem;
        color: #252B42;
        margin-bottom: 3rem;
        
    }
    .info p{
        padding-bottom: 3rem;
        font-size: 2rem;
        color: #737373
    }
    .card1{
        text-align: center;
        padding: 6rem 10rem;
    }
